.header {
    --iconSize: 50px;
    display: grid;
    grid-template-columns: auto 1fr auto auto auto auto;
    grid-template-areas: "logo search help color favorite layout";
    padding: 10px 15px;
    padding-right: 0;
    gap: 5px;
    background: linear-gradient(to right, #732ff9, #7532f9);
    z-index: 100;
    margin-bottom: 10px;
    position: fixed;
    box-sizing: border-box;
    width: 100%;
    left: 0;
    top: 0;

    @media (max-width: 400px) {
        --iconSize: 40px;
    }

    &.is-sticky {
        box-shadow: 0 2px 8px rgba(#732ff9, 0.30);
    }
}

.logo {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    grid-area: logo;

    svg {
        fill: #fff;
    }
}

.input-filter {
    grid-area: search;
    background: #fff;
    justify-self: center;
    border-radius: 4px;
    height: var(--iconSize);
    padding: 0 15px;
    max-width: 400px;
    width: 100%;
    display: block;
    border: 0;
    box-sizing: border-box;
    margin-bottom: 0;
    font-size: 15px;
    font-family: "Inter", sans-serif;
    color: #64748B;
    background-repeat: no-repeat;

    &:focus {
        outline: none;
        background: #fafafa;
    }
}

%button {
    cursor: pointer;
    width: var(--iconSize);
    height: var(--iconSize);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: none;
    border: 0;
    padding: 0;

    svg {
        width: 20px;
        height: 20px;
        fill: #fff;
    }
}

.btn-help {
    grid-area: help;
    @extend %button;
}

.btn-toggle-layout {
    grid-area: layout;
    @extend %button;
}

.btn-toggle-layout-big {
    display: none;
}

.btn-toggle-layout-small {
    display: block;
}

