    background-size: 22px;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    cursor: pointer;
}

.input-color-picker-inside {
    position: absolute;
    right: 0;
    top: 100%;
    transform: translate(0, -200%);
    pointer-events: none;
    opacity: 0;
}

.input-color-picker.is-active .input-color-picker-inside {
    pointer-events: all;
    opacity: 1;
    transform: translate(0);
}

.btn-favorite {
    grid-area: favorite;
    @extend %button;
}

.btn-favorite .btn-favorite-inactive {
    display: block;
}

.btn-favorite .btn-favorite-active {
    display: none;
}

.btn-favorite.is-active .btn-favorite-inactive {
    display: none;
}

.btn-favorite.is-active .btn-favorite-active {
    display: block;
}

.btn-favorite:disabled {
    opacity: 0.3;
}